.project-gallery {
  width: 100%;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 240px));
  gap: 2rem;
  padding: 1rem 0;
}
/* Card styles */
.project-card {
  text-decoration: none;
  color: inherit;
  position: relative;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.08);
  overflow: hidden;
  transition: transform 0.2s, box-shadow 0.2s;
  min-height: 160px;
  max-width: none; 
  display: block;
  flex-direction: column;
  justify-content: flex-end;
}


.project-card:hover {
  transform: translateY(-6px) scale(1.03);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.13);
}

.project-card-content {
  padding: 1.2rem 1rem 0.5rem 1rem;
  z-index: 2;
}

.project-title {
  margin: 0 0 0.5rem 0;
  font-size: 1.15rem;
  color: #222;
}

.project-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.project-tag {
  background: #ffffff;
  color: #fff;
  border-radius: 6px;
  padding: 0.2rem 0.7rem;
  font-size: 0.85rem;
  font-weight: 500;
}

/* Short description shown on hover */
.project-description {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 2.5rem;
  background: rgba(34, 34, 34, 0.96);
  color: #fff;
  padding: 1rem;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.25s;
  font-size: 0.98rem;
  min-height: 60px;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}

.project-card:hover .project-description {
  opacity: 1;
  pointer-events: auto;
}

/* Modal fallback styles (if used elsewhere) */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto; 
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-y: auto; /* Allow scrolling if content overflows */
  padding: 2rem 1rem; 
  z-index: 1000;
}

.modal-content-long{
  background: white;
  padding: 2rem;
  border-radius: 12px;
  max-width: 700px;
  width: 100%;
  max-height: 60vh; /* Prevent overflow beyond viewport */
  overflow-y: auto;  /* Enable scroll within the modal box */
  position: relative;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

.modal-content {
  background: white;
  padding: 2rem;
  border-radius: 12px;
  max-width: 700px;
  width: 100%;

  max-height: 50vh;       /* limit height to 90% of viewport height */
  overflow-y: auto;       /* enable vertical scrolling if content is taller */
  
  position: relative;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}


.modal-close-button {
  margin-top: 1rem;
}

.modal-link-button {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #333;
  text-decoration: none;
  font-size: 0.85rem;
  font-weight: 500;
  gap: 0.25rem;
}
.modal-links {
  display: flex;
  gap: 2rem;
  justify-content: left;
  margin: 1rem 0;
  color: #333;
}

.modal-banner-image {
  max-width: 100%;      /* Don't exceed the modal width */
  max-height: 300px;    /* Limit height so it doesn't get too tall */
  width: auto;          /* Maintain aspect ratio */
  height: auto;         /* Maintain aspect ratio */
  object-fit: contain;  /* Contain the entire image inside box */
  border-radius: 8px;   /* Optional: rounded corners */
  display: block;       /* Removes bottom gap */
  margin: 0 auto 1rem;  /* Center and add spacing below */
}


/* Hover card (Notion style) */
.hover-card {
  position: absolute;
  top: 100%;
  left: 0;
  width: 320px;
  background: white;
  border: 1px solid #ddd;
  border-radius: 12px;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.15);
  margin-top: 0.75rem;
  overflow: hidden;
  z-index: 20;
  display: flex;
  flex-direction: column;
}

/* Banner at the top of the hover card */
.hover-card-banner {
  height: 120px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #fff;
}

/* Body of the hover card */
.hover-card-body {
  padding: 1rem;
}

.hover-card-title {
  margin: 0.5rem 0;
  font-size: 1.1rem;
  font-weight: 600;
  color: #222;
}

.hover-card-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.4rem;
  margin-bottom: 0.5rem;
}

/* Icon links for GitHub and Paper */
.hover-card-links {
  display: flex;
  gap: 0.75rem;
  align-items: center;
  margin: 0.5rem 0;
}

.hover-card-icon {
  width: 24px;
  height: 24px;
  opacity: 0.85;
  transition: opacity 0.2s;
  cursor: pointer;
}

.hover-card-icon:hover {
  opacity: 1;
}

/* Full description text */
.hover-full-description {
  font-size: 0.9rem;
  line-height: 1.4;
  margin-top: 0.5rem;
  color: #333;
}

.pagination-controls {
  margin-top: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.pagination-controls button {
  background-color: #2b6cb0;
  color: white;
  border: none;
  border-radius: 6px;
  padding: 0.4rem 0.8rem;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s;
}

.pagination-controls button:disabled {
  background-color: #bbb;
  cursor: default;
}
